// Interactive Form
export const InteractiveForm: Story = {
  render: () => {
    const [formData, setFormData] = useState({
      firstName: '',
      lastName: '',
      email: '',
      phone: '',
      website: '',
      message: ''
    })
    
    const [showSuccess, setShowSuccess] = useState(false)
    
    const handleSubmit = (e: React.FormEvent) => {
      e.preventDefault()
      setShowSuccess(true)
      setTimeout(() => setShowSuccess(false), 3000)
    }
    
    return (
      <form onSubmit={handleSubmit} className="w-96 space-y-6">
        {showSuccess && (
          <div className="p-4 bg-green-500/20 border border-green-500/30 rounded-lg text-green-300 text-sm">
            âœ… Form submitted successfully!
          </div>
        )}
        
        <div className="grid grid-cols-2 gap-4">
          <LiquidInput
            label="First Name"
            placeholder="John"
            value={formData.firstName}
            onChange={(e) => setFormData(prev => ({ ...prev, firstName: e.target.value }))}
            required
          />
          
          <LiquidInput
            label="Last Name"
            placeholder="Doe"
            value={formData.lastName}
            onChange={(e) => setFormData(prev => ({ ...prev, lastName: e.target.value }))}
            required
          />
        </div>
        
        <LiquidInput
          type="email"
          label="Email Address"
          placeholder="john@example.com"
          value={formData.email}
          onChange={(e) => setFormData(prev => ({ ...prev, email: e.target.value }))}
          required
          leftIcon={
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 12a4 4 0 10-8 0 4 4 0 008 0zm0 0v1.5a2.5 2.5 0 005 0V12a9 9 0 10-9 9m4.5-1.206a8.959 8.959 0 01-4.5 1.207" />
            </svg>
          }
          fullWidth
        />
        
        <LiquidInput
          type="tel"
          label="Phone Number"
          placeholder="(555) 123-4567"
          value={formData.phone}
          onChange={(e) => setFormData(prev => ({ ...prev, phone: e.target.value }))}
          leftIcon={
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
            </svg>
          }
          helperText="Auto-formatted as you type"
          fullWidth
        />
        
        <LiquidInput
          type="url"
          label="Website"
          placeholder="https://example.com"
          value={formData.website}
          onChange={(e) => setFormData(prev => ({ ...prev, website: e.target.value }))}
          leftIcon={
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9v-9m0-9v9m0 9c-5 0-9-4-9-9s4-9 9-9" />
            </svg>
          }
          helperText="Optional website URL"
          fullWidth
        />
        
        <button
          type="submit"
          className="w-full px-4 py-3 bg-blue-500/20 border border-blue-500/30 rounded-lg text-blue-300 font-medium hover:bg-blue-500/30 transition-colors"
        >
          Submit Form
        </button>
      </form>
    )
  },
  parameters: {
    docs: {
      description: {
        story: 'Complete interactive form with validation, auto-formatting, and real-time feedback.'
      }
    }
  }
}

